<script lang="ts" setup>
import { getRepoContent, IListUserRepoItemRes, listUserRepo } from '@/apis';
import DPlayer from '@/components/DPlayer.vue';
import userLoading from '@/hooks/useLoading';
import userListRepo, { filterRepoList } from '@/hooks/userListRepo';
import { useTargetState } from '@/stores';
import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc';
import { onMounted, ref, watch } from 'vue';

dayjs.extend(utc);
let treeData = ref<Array<IListUserRepoItemRes>>([]);

let target = useTargetState();

let repoTotal = ref<number>(0); // 
let currentPage = ref<number>(1); // 
let pageSize = ref<number>(30); // 
let leftScrollRef = ref(null);
let isCollapse = ref(true);
let videoForm = ref(null);
let dPlayerRef = ref(null);

const loading = ref(false);

const [error, data] = await userListRepo();
if (!error && data) {
    const { userRepoList, total } = data;
    treeData.value = userRepoList.value;
    repoTotal.value = total.value;
}

watch([pageSize, currentPage], async ([pageSize, currentPage]) => {
    const loading = userLoading();
    const [error, data] = await listUserRepo({
        page: currentPage,
        per_page: pageSize,
    });
    if (error) {
        loading.close();
        return Promise.reject(error);
    }

    treeData.value = filterRepoList(data);
    loading.close();
});

async function onOpenSubMenus(index: string, indexPath: string[]) {
    console.log(index, indexPath);
    loading.value = true;
    const [error, data] = await getRepoContent(index);
    if (error) {
        loading.value = false;
        return Promise.reject(error);
    }

    treeData.value.forEach((item) => {
        if (item.name === index) {
            item.children = data.map((itm) => {
                return { name: itm.name, path: `${itm.path}` };
            });
        }
    });

    loading.value = false;
}

function onClickMenuItem({
    indexPath,
}: {
    index: string;
    indexPath: string[];
}) {
    target.value.repoName = indexPath[0];
    target.value.directoryName = indexPath[1];

    const dpIns = dPlayerRef.value as any;
    if (dpIns) {
        dpIns.switchVideo(
            target.value.username,
            target.value.repoName,
            target.value.directoryName,
        );
    }
    console.log('target::', target.value);
}

onMounted(() => {
    console.log('dPlayerRef::', dPlayerRef);
});
</script>

<template>
    <el-container style="height: 100%">
        <el-aside style="width: auto">
            <el-scrollbar ref="leftScrollRef">
                <el-menu
                    :collapse="isCollapse"
                    class="el-menu-vertical"
                    menu-trigger="click"
                    @open="onOpenSubMenus"
                >
                    <el-sub-menu
                        v-for="item in treeData"
                        :key="item.id"
                        :index="item.name"
                    >
                        <template #title>
                            <el-icon><i-ep-takeaway-box /></el-icon>
                            <span v-show="!isCollapse"> {{ item.name }}</span>
                        </template>
                        <div v-loading="loading">
                            <template v-if="item.children">
                                <el-menu-item
                                    v-for="menu in item.children"
                                    :key="menu.name"
                                    :index="menu.path"
                                    @click="onClickMenuItem"
                                    ><el-icon><i-ep-folder-opened /></el-icon>
                                    {{ menu.name }}
                                </el-menu-item>
                            </template>
                            <template v-else></template>
                        </div>
                    </el-sub-menu>
                </el-menu>
            </el-scrollbar>
        </el-aside>
        <el-container>
            <el-header style="padding-left: 0px">
                <el-button
                    size="large"
                    text
                    style="height: 100%; border-radius: 0"
                    @click="
                        () => {
                            isCollapse = !isCollapse;
                        }
                    "
                >
                    <template #icon>
                        <el-icon style="font-size: 24px">
                            <i-ep-expand />
                        </el-icon>
                    </template>
                </el-button>
            </el-header>
            <el-main>
                <d-player ref="dPlayerRef"></d-player>
            </el-main>
            <el-footer>
                <el-pagination
                    v-model:currentPage.sync="currentPage"
                    background
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[30, 50, 80, 100]"
                    :page-size="pageSize"
                    :total="repoTotal"
                    @update:page-size="(val:number)=>{pageSize = val;}"
                    @update:current-page="(val:number)=>{currentPage = val;}"
                />
            </el-footer>
        </el-container>
    </el-container>
</template>

<style scoped>
.el-menu-vertical:not(.el-menu--collapse) {
    width: 200px;
}
.el-menu-vertical.el-menu--collapse {
    width: 0;
}
.el-sub-menu__title {
    padding: 0px 40px 0px 20px !important;
}

.el-footer > .el-pagination {
    justify-content: center;
}

@media screen and (max-width: 1080px) {
    .el-footer {
        overflow-x: auto;
    }
    .el-footer > .el-pagination {
        justify-content: flex-start;
    }
}
</style>
