import { getPublicUserInfo, IListUserRepoItemRes, listUserRepo } from '@/apis';
import userLoading from '@/hooks/useLoading';
import to from 'await-to-js';
import { ref } from 'vue';

export default async function () {
    const total = ref<number>(0); // 
    const userRepoList = ref<Array<IListUserRepoItemRes>>([]);

    const loading = userLoading();

    const [error1, userInfo] = await getPublicUserInfo();
    const [error2, repoList] = await listUserRepo({});

    if (error1 || error2) {
        loading.close();
        return to(Promise.reject(error1 || error2));
    }

    total.value = userInfo.public_repos;
    userRepoList.value = filterRepoList(repoList);
    loading.close();
    return to(
        Promise.resolve({
            total,
            userRepoList,
        }),
    );
}

export function filterRepoList(repoList: IListUserRepoItemRes[]) {
    return repoList
        .map(
            ({
                id,
                name,
                node_id,
                full_name,
                size,
                html_url,
                updated_at,
                created_at,
            }) => {
                return {
                    id,
                    node_id,
                    name,
                    full_name,
                    size,
                    html_url,
                    updated_at,
                    created_at,
                };
            },
        )
        .filter((item) => item.name.startsWith('v'));
}
