<template>
    <div class="p-4">
        <div ref="videoRef"></div>
    </div>
</template>
<script lang="ts" setup>
import { useTargetState } from '@/stores';
import DPlayer from 'dplayer';
import Hls from 'hls.js';
import { onMounted, ref } from 'vue';

const target = useTargetState();

function createDPlayer(
    container: any,
    options: {
        username: string;
        repoName: string;
        directoryName: string;
    },
) {
    const map: {
        [key: string]: (
            username: string,
            repoName: string,
            directoryName: string,
        ) => string;
    } = {
        github(username: string, repoName: string, directoryName: string) {
            return `https://raw.githubusercontent.com/${username}/${repoName}/main/${directoryName}/playlist.m3u8`;
        },
        HK1(username: string, repoName: string, directoryName: string) {
            return `https://ghproxy.fsofso.com/https://github.com/${username}/${repoName}/blob/main/${directoryName}/playlist.m3u8`;
        },
        HK2(username: string, repoName: string, directoryName: string) {
            return `https://raw.iqiq.io/${username}/${repoName}/main/${directoryName}/playlist.m3u8`;
        },
        HK3(username: string, repoName: string, directoryName: string) {
            return `https://hk1.monika.love/${username}/${repoName}/main/${directoryName}/playlist.m3u8`;
        },
        jsDelivr(username: string, repoName: string, directoryName: string) {
            return `https://cdn.jsdelivr.net/gh/${username}/${repoName}/${directoryName}/playlist.m3u8`;
        },
        other(username: string, repoName: string, directoryName: string) {
            return `https://raw.githubusercontents.com/${username}/${repoName}/main/${directoryName}/playlist.m3u8`;
        },
    };

    function createQualityList(
        username: string,
        repoName: string,
        directoryName: string,
    ) {
        return Object.keys(map).map(function (prop) {
            const res = map[prop](username, repoName, directoryName);
            return {
                name: prop,
                url: res,
                type: 'customHls',
                customType: {
                    customHls: function (video: any, player: any) {
                        const hls = new Hls();
                        hls.loadSource(video.src);
                        hls.attachMedia(video);
                    },
                },
            };
        });
    }

    const qualityList = createQualityList(
        options.username,
        options.repoName,
        options.directoryName,
    );

    const dplayer = new DPlayer({
        container,
        autoplay: false,
        theme: '#0093ff',
        loop: true,
        lang: 'zh-cn',
        screenshot: false,
        hotkey: true,
        preload: 'auto',
        volume: 0.7,
        mutex: true,
        video: {
            url: qualityList[0].url,
            quality: qualityList,
            defaultQuality: 0,
            type: 'customHls',
            customType: {
                customHls: function (video: any, player: any) {
                    const hls = new Hls();
                    hls.loadSource(video.src);
                    hls.attachMedia(video);
                    console.log(video, player);
                },
            },
        },
        contextmenu: [
            {
                text: 'origin',
                link: qualityList[0].url,
            },
        ],
    });

    return dplayer;
}

const videoRef = ref(); // 
let dIns: any;

onMounted(() => {
    dIns = createDPlayer(videoRef.value, {
        username: target.value.username,
        repoName: target.value.repoName,
        directoryName: target.value.directoryName,
    });
});

defineExpose({
    switchVideo(username: string, repoName: string, directoryName: string) {
        dIns.destroy();
        dIns = createDPlayer(videoRef.value, {
            username,
            repoName,
            directoryName,
        });
    },
});
</script>
<style>
.p-4,
.dplayer {
    height: 60vh;
}
</style>
